<template>
  <v-app id="main-view">
    <!-- <core-filter /> -->
    <core-toolbar />
    <v-content
      :style="'padding-left: 0px;'">
      <v-fade-transition mode="out-in">
        <router-view />
      </v-fade-transition>
    </v-content>
    <!-- <core-footer v-if="$route.meta.name !== 'Maps'" /> -->
    <Chatroom v-show="chatView"/>
    <div
      id='chat-icon'
      @click="doOpenChat()"
      >
      <v-icon
        v-if="!chatView"
        class="cursor-no-hover text-white mr-1 active">mdi-message-processing</v-icon>
      <v-icon
        v-if="chatView"
        class="cursor-no-hover text-white mr-1 close">mdi-close</v-icon>
      <!-- <img
        :src="ChatIcon"
        class="cursor-no-hover active"> -->
    </div>
    <core-footer />
  </v-app>
</template>

<script>
import { mapState } from 'vuex'
import Chatroom from '../components/Plugins/Chatroom.vue'
import ChatIcon from '@/assets/img/chat_icon.png'
export default {
  name: 'MainView',
  components: {
    Chatroom
  },
  metaInfo () {
    return {
      title: this.appName
    }
  },
  data: () => ({
    ChatIcon,
    chatView: false
  }),
  computed: {
    ...mapState(['appName', 'dologinView', 'doChatView'])
  },
  watch: {
    doChatView (val) {
      this.chatView = val
    },
    chatView (val) {
      this.$store.state.doChatView = val
    }
  },
  methods: {
    doOpenChat () {
      if (localStorage.getItem('token')) {
        this.chatView = !this.chatView
      } else {
        this.$store.state.dologinView = true
      }
    }
  }
}
</script>
