    width: 80px;
    height: 80px;
    border-radius: 50%;
    font-size: 45px;
    opacity: 1;
    &.close{
      background-color: #7a7a7a;
    }
    &.active{
      animation-name: chat_btn;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      z-index: 999;
      @keyframes chat_btn{
        0%{
          bottom: 10px;
          background-color: $chat-color;
        }
        50%{
          bottom: 15px;
          background-color: #fe4545;
        }
        100%{
          bottom: 10px;
          background-color: $chat-color;
        }
      }
    }
  }
  >img{
    position: fixed;
    bottom: 0px;
    right: 0px;
    width: 80px;
    height: 80px;
    &.active{
      animation-name: chat_icon;
      animation-duration: 0.5s;
      animation-iteration-count:infinite;
      z-index: 999;
      @keyframes chat_icon{
        0%{
          bottom: 0px;
          opacity: 0.8;
        }
        50%{
          bottom: 5px;
          opacity: 1;
        }
        100%{
          bottom: 0px;
          opacity: 0.8;
        }
      }
    }
  }
}
